    <div class="button" @click="handleConfirm">{{ $t('admin.release') }}</div>
  </div>
</template>

<script>
import { handleMining } from '@/services/warehourse'
import { handleAddTask } from '@/services/task'
export default {
  name: '',
  data () {
    return {
      tokenList: [], // 代币列表
      daySelect: 7, // 任务有效期 index
      auditSelect: 1, // 审核时间 index
      dataForm: {
        title: '', // 标题
        content: '', // 详情
        taskDuration: 7 * 24 * 60, // 任务有效期 单位:分钟
        buttonName: '', // 任务按钮名称
        url: '', // 任务链接
        reviewTime: 60, // 审核时间(分钟)
        tokenType: null,
        times: null, // 任务份数
        award: null // 每份奖励
      }
    }
  },
  methods: {
    handleDaySelect (key) {
      this.daySelect = key
      this.dataForm.taskDuration = key * 24 * 60
    },
    handleAuditSelect (key) {
      this.auditSelect = key
      this.dataForm.reviewTime = key * 60
    },
    async getTokenList () {
      const resp = await handleMining()
      this.dataForm.tokenType = resp.data.supportTokenList[0].tokenType
      this.tokenList = resp.data.supportTokenList.map(item => {
        return {
          value: item.tokenType,
          text: item.tokenName
        }
      })
    },
    async handleConfirm () {
      if (!this.dataForm.title || !this.dataForm.content || !this.dataForm.buttonName || !this.dataForm.url || !this.dataForm.times || !this.dataForm.award) {
        return this.$toast(this.$t('common.fill'))
      }
      const resp = await handleAddTask(this.dataForm)
      console.log(resp)
      if (!resp.success) return
      this.$gbUtils.handleToPage('/agency')
    }
  },
  computed: {
    totalAward () { // 需要支付代笔数量
      return this.dataForm.times * this.dataForm.award
    }
  },
  mounted () {
    this.getTokenList()
  }
}
</script>

